@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
    --purple: rgb(36,14,55);
    --green: rgb(128,228,137);
    --roboto: 'Roboto', sans-serif;
    --bebas: 'Bebas Neue', cursive;
    --openSans: 'Open Sans Condensed', sans-serif;
}

/* Loading bar styling */
.progress div {
    width: 100%;
    background-color: rgb(128,228,137);
    background-color: var(--green);
}

.loading-message p {
    margin: 0.5rem 0;
}

.background_image {
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-repeat: no-repeat;
    position: fixed;
    overflow-y: scroll;
    object-fit: cover;
    padding: 1rem 0 1rem;
    background-image: url(/team-selector-frontend/static/media/evening.31a3620c.jpg);
    background-color: var(--purple);
}







* {
    box-sizing: border-box;
}

.container {
    background-color: white;
    opacity: 0.95;
    width: 50%;
    border-radius: 5px;
}

@media screen and (max-width: 767px) {
    .container {
        width: 85%;
    }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
    .container {
        width: 75%;
    }
}

.history-table {
    width: 75%;
}

@media screen and (max-width: 991px){
    .history-table {
        width: 95%;
    }
}

.main-title {
    background-color: rgb(36,14,55);
    padding: 2rem 0rem;
    border: 0.8rem rgb(128, 228, 137) solid;
    margin-bottom: 1.5rem;
}

.container {
    padding-top: 1.3rem;
    padding-bottom: 1.3rem;
}

/* buttons and link layouts */

.adjacent-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.central-button {
    text-align: center;
}


/*title typography styling*/
.title-text {
    color: white;
    text-align: center;
    font-family: var(--bebas);
}

.title-text h1 {
    font-size: 4rem;
    letter-spacing: 0.03em;
}

.title-text h5 {
    font-size: 2rem;
}

/*all containers typography styling*/
.container {
    font-family: var(--openSans);
    font-size: 1.2rem;
}

/* game history styling */

@media screen and (max-width: 767px) {
    .history-table {
        font-size: 1rem;
    }
}

@media screen and (max-width: 321px) {
    .history-table {
        font-size: 0.9rem;
    }
}

/* settings name-picker and instructions styling*/
.team-name-list {
    list-style-type: none;
    padding: 0px;
    margin-bottom: 0.5rem; 
}

.instructions-para {
    margin-bottom: 0.5rem;
}

.name-picker-text {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.name-picker-para {
    margin-bottom: 0.2rem;
    text-align: center;
    align-self: center;
}

.instructions-heading {
    text-align: center;
}

/*team selection styling*/
.card {
    border-top: none;
}

.card-title {
    background-color: var(--purple);
    padding: 0.3rem;
    margin: 0;
    color: var(--green);
}

.list-group-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: none;
}

.list-group-item-text {
    margin-bottom: 0.5rem;
}

.average-info {
    padding: 0.4rem;
    margin: 0;
    background-color: var(--green);
    color: var(--purple);
}

/*form errors styling*/
.formErrors p {
    margin-bottom: 0.5rem;
    padding: 8px 20px;
}

.alert {
    background-color: rgba(106, 57, 149, 0.4);
}

/*copied message styling*/
.copied-msg {
    margin-left: 1rem;
    color: red;
}

.about-list {
    list-style: none;
}

.about-list li::before {
    content: "\2022";
    color: var(--green);
    font-size: 1.5em;
    font-weight: bold;
    display: inline-block; 
    width: 1em;
    margin-left: -1em;
}
.btn {
    font-size: 1.2rem;
    border-radius: 5px;
}
/* importants added to override bootstrap styling for buttons */
.btn:focus, .btn:active {
    outline: none !important;
    box-shadow: none !important;
  }

.btn-danger, .btn-primary:hover, .btn-danger:disabled, .btn-danger:active, .btn-danger:focus {
    background-color: var(--purple) !important;
    border: var(--green) solid 2px !important;
    color: var(--green) !important;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
}

.btn-primary, .btn-danger:hover, .btn-primary:disabled, .btn-primary:active, .btn-primary:focus {
    background-color: var(--green) !important;
    border: var(--purple) solid 2px !important;
    color: var(--purple) !important;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
}

.gapped-buttons {
    margin-right: 2rem;
}
.form-check {
    padding: 0px;
}

.form-check input {
    margin-left: 0.5rem;
}

/*overriding bootstrap form colours and focus colours*/
.custom-range::-webkit-slider-thumb  {
    background: var(--purple);
}

.custom-range::-moz-range-thumb {
    background: var(--purple);
}
  
.custom-range::-ms-thumb {
    background: var(--purple);
}

.form-control:focus {
    border-color: var(--purple) !important;
    box-shadow: 0 0 0 0.2rem rgba(36, 14, 55, 0.25) !important;
}

/*name and ability form controls side by side using flex row*/
.player-form {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

/*responsive styling to ensure name and ability form controls are in a column on small devices*/
@media screen and (max-width: 767px) {
    .player-form {
        flex-direction: column;
    }
    .player-form .form-group {
       align-self: center; 
       width: 100%;
    }
}

/*adjusting width according to whether ability range button is present*/
.no-ability-pick {
    width: 100%;
}

.ability-pick {
    width: 40%;
}

.custom-range {
    margin-top: 0.5rem;
}



